function App() {
  return (
    <>
      <HashRouter>
        <Navigation />
        <Route path="/" exact={true} component={Home} />
        <Route path="/about" component={About} />
        <Route path="/movie/:id" component={Detail} />
        {/* <Route path="/movie-detail" component={Detail} /> */}
        {/* 두 경로가 /about /home처럼 완전 다르면 문제가 없지만
      /about /이런 식이면 두 컴포넌트가 겹쳐서 보일 수 있다
      Navigation에는 props가 없다.
      route에 props가 있다.
      */}
      </HashRouter>
      <footer></footer>
    </>
  );
}

export default App;
